import React from "react";
import {BrowserRouter as Router, Switch,Route} from "react-router-dom";
import { Navbar, Nav, NavDropdown, Container } from "react-bootstrap";
import 'bootstrap/dist/css/bootstrap.min.css';
import Home  from "./Home";
import PageNotFound from "./PageNotFound";
import Autos from "./Autos";
import Motos from "./Motos";
import Camionetas from "./Camionetas";
import About from "./About";


const Menu = () =>{
    return(
<Router>
<Navbar sticky="top" collapseOnSelect expand="lg" className="colorMenu" variant="dark">
  <Container>
  <Navbar.Brand href="/">Inicio</Navbar.Brand>
  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
  <Navbar.Collapse id="responsive-navbar-nav">
    <Nav className="me-auto">
      <NavDropdown title="Vehiculos" id="basic-nav-dropdown">
      <NavDropdown.Item href="/Autos/">Autos</NavDropdown.Item>
      <NavDropdown.Item href="/Motos/">Motos</NavDropdown.Item>
      <NavDropdown.Item href="/Camionetas/">Camionetas</NavDropdown.Item>
      </NavDropdown>
    </Nav>
    <Nav>
    <Nav.Link href="/About/">Acerca de</Nav.Link>
    </Nav>
  </Navbar.Collapse>
  </Container>
</Navbar>
 
  <Switch>
    <Route path="/" exact component={Home}/>
    <Route path="/Autos/" exact component={Autos}/>
    <Route path="/Motos/" exact component={Motos}/>
    <Route path="/Camionetas/" exact component={Camionetas}/>
    <Route path="/About/" exact component={About}/>
    <Route component={PageNotFound}/>
  </Switch>
</Router>
)
};



export default Menu